<template>
    <ItemDetail
        :previewHeight="null"
        :aspectRatio="'1 / 1'"
        :invertShadow="sameColors === null ? null : !sameColors"
        v-if="data.ready">
        <template v-slot:background>
            <Img
                v-if="data.item?.cover"
                :preview="data.item?.cover?.crop"
                :original="data.item?.cover?.original"
                :skeleton="true"
                :checktone="true"
                :wathermark="data.item.type === 'EDITORIAL' ? 'playlists-top' : null"
                @tone="data.tone = $event"
            />

            <div v-if="data.item.type === 'EDITORIAL'" class="editorial-logo">
                <Icon icon="editorial" />
                <Icon icon="editorial" />
            </div>
        </template>
        <template v-slot:preview>
            <div class="buttons" v-if="data.tone">
                <IconButton :name="t('player.shuffle')"
                    :class="{ invert: !sameColors }"
                    class="tertiary size-xl dirty" icon="random"
                    :dunk="true"
                    @click="playAndShuffle"
                />

                <AudioToggle
                    :state="state"
                    :light="true"
                    class="size-l secondary dirty"
                    :class="{ invert: data.tone !== 'dark' }"
                    @play="play"
                    @pause="pause"
                />
            </div>
        </template>
        <template v-slot:toolbar>
            <template v-if="data.tone">
                <IconButton
                    icon="unlike"
                    class="tertiary size-xl"
                    :class="{ invert: !sameColors }"
                    :dunk="true"
                    @click="like"
                />

                <IconButton
                    icon="tg-stars"
                    class="tertiary size-xl"
                    :dunk="true"
                    @click="like"
                />

                <IconButton
                    icon="apps"
                    class="tertiary size-l"
                    :class="{ invert: !sameColors }"
                    @click="openKebab"
                />
            </template>
        </template>
        <template v-slot:detail>
            <Tracks
                v-if="data.item?.id"
                :getById="data.item.id"
                :loadMore="false"
                :source="{
                    store: 'playlists',
                    fnName: 'getTracks'
                }"
                :options="{
                    type: 'playlist',
                    id: data.item.id
                }"
                :showKebab="true"
                :title="{ text: locale.title(data.item.title) }"
            />
        </template>
    </ItemDetail>

    <Ready :page="`release-${props.id}`" :init="init" />
</template>

<script setup>
    import { onMounted, onBeforeUnmount, reactive, computed } from 'vue';
    import api from 'ðŸ”—/api';
    import stores from '@/stores';
    import Ready from 'ðŸ”—/components/ui/Ready.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import ItemDetail from 'ðŸ”—/components/ui/ItemDetail.vue';
    import AudioToggle from 'ðŸ”—/components/ui/AudioToggle.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';

    import Tracks from 'ðŸ”—/components/blocks/Tracks.vue';
    import { useI18n } from 'vue-i18n';
    import { message } from 'ðŸ”—/utils/utils';

    const { t } = useI18n();

    const locale = stores.locale();
    const tg = stores.tg();
    const modals = stores.modals();
    const others = stores.others();
    const player = stores.player();

    const props = defineProps({
        id: { type: String }
    });

    const data = reactive({
        ready: false,
        item: null,
        tone: null
    });

    const state = computed(() => {
        if (!data.item?.id || !player.panes.default || player.panes.default._queue?.id !== data.item?.id) {
            return;
        }

        return player.panes.default._state;
    });

    const sameColors = computed(() => {
        if (data.tone === null) {
            return null;
        }

        return (others.theme !== 'dark' && data.tone !== 'dark') || (others.theme === 'dark' && data.tone === 'dark');
    })

    const playAndShuffle = async () => {
        if (state.value) {
            player.stop();
            player.queue.clear();
        }

        player.playlist.play(data.item.id, { shuffle: true });
        return;
    }

    const pause = async () => {
        player.playlist.pause(data.item.id);
    }

    const play = async () => {
        player.playlist.play(data.item.id);
    }

    const like = (e) => {
        message.info(t('common.soon'))
    }

    const openKebab = () => {
        modals.open('playlist-kebab', { quietClose: true, data: {
            id: props.id,
            item: data.item
        } })
    }

    const init = async () => {
        data.item = await api.playlists().get(props.id);
        data.ready = true;
    }

    onMounted(async () => {
        tg.showSettingsButton(openKebab);
    });

    onBeforeUnmount(() => {
        tg.hideSettingsButton(openKebab);
    });
</script>


<style lang="scss" scoped>
    .title {
        font: var(--font-h4);
        color: white; //var(--white-500);
        font-weight: bold;
        text-align: center;
        -webkit-text-stroke: 1px rgb(0, 0, 0, .2);
    }

    .buttons {
        min-height: 38px;
        display: flex;
        flex-direction: row;
        gap: 8px;
        align-items: center;
        justify-content: center;

        button {
            // min-width: 132px;
        }
    }

    .editorial-logo {
        position: absolute;
        left: 32px;
        top: 24px;
        width: 100px;
        height: 100px;
        opacity: .6;
        svg {
            display: block;
            fill: var(--dark-80);
        }
        @include svg-skeleton(3s, 25deg, 0);
    }
</style>